import styled from '@emotion/styled';

export const ImageGallery = styled.ul`
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
`;

export const SpinnerDiv = styled.div`
  width: 80px;
  height: 80px;
  margin: 0 auto;
`;

export const EmptyResponse = styled.h1`
  text-align: center;
`;
